.s-card {
  cursor: pointer;
  height: 100px;
  width: 75px;
  border: 1px solid #d0e8ef;
  margin: auto;
  padding: 5px;
}
.s-card .thub-holder {
  display: block;
  height: 74px;
}
.s-card .place-holder {
  display: block;
  height: 65px;
  border: 1px solid #ccc;
  box-sizing: content-box;
  margin-bottom: 3px;
}
.s-card .place-holder .thumb {
  font-size: 40px;
  padding-top: 12px;
}

.s-card .thub-holder img {
  height: 70px;
}
.s-card .name {
  display: block;
  font-size: 12px;
  line-height: 16px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
